	import {  
        mapMutations  
    } from 'vuex';
	import uniRequest from 'uni-request';
	export default{
		data(){
			return {
				mobile: '',
				password: '',
				logining: false,
				openid:''
			}
		},
		onLoad(options){
			console.log(options)
			this.openid = options.openid
			this.message()
		},
		methods: {
			...mapMutations(['login']),
			message(){
				this.$api.msg('请绑定手机号')
			},
			inputChange(e){
				const key = e.currentTarget.dataset.key;
				this[key] = e.detail.value;
			},
			navBack(){
				uni.navigateBack();  
			},
			
			changePayType(){
				this.remember = !this.remember
				console.log(this.remember)
			},
			
			async toLogin(){
				this.logining = true;
				const {mobile, password} = this;
				// 手机号验证
				if(!mobile){ 
					this.$api.msg('请输入手机号');
					this.logining = false;
					return
				}
				// 密码验证
				if(!password){
					this.$api.msg('请输入密码');
					this.logining = false;
					return
				}else if(password.length < 8){
					this.$api.msg('密码不能少于8位');
					this.logining = false;
					return
				}
				
				const sendData = {
					mobile,
					password,
					openid:this.openid
				};
				console.log(sendData)
				uniRequest.post('/oauth/qq/user/openid/',sendData).then(res=>{
					console.log(res)
					this.login(res.data);
					uni.navigateBack();  
					/* uni.navigateTo({
						url: "/pages/user/user"
					}) */
				}).catch(error=>{
					console.log(error)
				})
				
				/* if(response.status === 200){
					if(this.remember === true){
						
						uni.navigateBack();  
					}
					
				}else{
					this.$api.msg(response.msg);
					this.logining = false;
				} */
			},
		},

	}
